import { Disclosure, Menu } from '@headlessui/react'
import { Bars3Icon, XMarkIcon, ChevronDownIcon } from '@heroicons/react/24/outline'
import { useLocation } from 'react-router-dom'

const navigation = [
  { name: '', href: '/' },
  { name: '', href: '/rules' },
  { name: ' ', href: '/faq' },
]

const servers = [
  { name: '', href: '/dominion' },
  { name: '', href: '/subserver' },
]

const Header = () => {
  const location = useLocation()

  const isActive = (href) => location.pathname === href

  return (
    <Disclosure as="nav" className="bg-green-900/10 z-50 relative">
      {({ open }) => (
        <>
          <div className="max-w-7xl mx-auto px-4 flex justify-between items-center py-4">
            <a href="/" className="flex items-center text-gray-200 font-bold text-lg">
              <img src="/logo512.png" alt="M4SUB" className="w-8 h-8 mr-2" />
              M4SUB
            </a>

            <div className="hidden md:flex space-x-4 items-center uppercase font-bold">
              {navigation.map((item) => (
                <a
                  key={item.name}
                  href={item.href}
                  className={`px-4 py-2 hover:bg-gray-700 transition ${isActive(item.href) ? 'bg-[#c5629a] pointer-events-none' : ''}`}
                >
                  {item.name}
                </a>
              ))}

              <Menu as="div" className="relative">
                <Menu.Button className="px-4 py-2 flex items-center hover:bg-gray-700 transition">
                  
                  <ChevronDownIcon className="w-4 h-4 ml-2" />
                </Menu.Button>

                <Menu.Items className="absolute left-0 mt-2 w-40 bg-green-900/90 shadow-lg">
                  {servers.map((server) => (
                    <Menu.Item key={server.name}>
                      {({ active }) => (
                        <a
                          href={server.href}
                          className={`block px-4 py-2 ${active ? 'bg-gray-700' : ''} ${isActive(server.href) ? 'bg-[#c5629a] pointer-events-none' : ''}`}
                        >
                          {server.name}
                        </a>
                      )}
                    </Menu.Item>
                  ))}
                </Menu.Items>
              </Menu>

              <a
                href="https://discord.gg/2TxYxe7cbp"
                target="_blank"
                rel="noreferrer"
                className="px-4 py-2 bg-[#5865f2] hover:bg-[#f390d0] text-gray-200 transition flex items-center"
              >
                <img src="/icons/light-discord.png" alt="Discord Icon" className="w-6 h-6 mr-2" />
                Discord
              </a>
            </div>

            <Disclosure.Button className="md:hidden flex items-center justify-center w-8 h-8 text-gray-200">
              {open ? <XMarkIcon className="w-6 h-6" /> : <Bars3Icon className="w-6 h-6" />}
            </Disclosure.Button>
          </div>

          <Disclosure.Panel className="md:hidden bg-green-900/90 space-y-1 px-2 py-4 uppercase font-bold">
            {navigation.map((item) => (
              <a
                key={item.name}
                href={item.href}
                className={`block px-4 py-2 hover:bg-gray-700 transition ${isActive(item.href) ? 'bg-[#c5629a] pointer-events-none' : ''}`}
              >
                {item.name}
              </a>
            ))}

            <Menu as="div" className="relative">
              <Menu.Button className="w-full text-left px-4 py-2 hover:bg-gray-700 transition flex items-center">
                
                <ChevronDownIcon className="w-4 h-4 ml-2" />
              </Menu.Button>
              <Menu.Items className="w-full bg-green-900/90 mt-1">
                {servers.map((server) => (
                  <Menu.Item key={server.name}>
                    {({ active }) => (
                      <a
                        href={server.href}
                        className={`block px-4 py-2 ${active ? 'bg-gray-700' : ''} ${isActive(server.href) ? 'bg-[#c5629a] pointer-events-none' : ''}`}
                      >
                        {server.name}
                      </a>
                    )}
                  </Menu.Item>
                ))}
              </Menu.Items>
            </Menu>

            <a
              href="https://discord.gg/2TxYxe7cbp"
              target="_blank"
              rel="noreferrer"
              className="block px-4 py-2 bg-[#5865f2] hover:bg-[#f390d0] text-gray-200 transition flex items-center"
            >
              <img src="/icons/light-discord.png" alt="Discord Icon" className="w-6 h-6 mr-2" />
              Discord
            </a>
          </Disclosure.Panel>
        </>
      )}
    </Disclosure>
  )
}

export default Header
